ection: column;\n    margin: $margin-mobile;\n  }\n}","//flex\n@mixin flex($direction:row,\n$justify-content:flex-start,\n$align-items:flex-start) {\n  display: flex;\n  flex-direction: $direction;\n  align-items: $align-items;\n  justify-content: $justify-content;\n}\n\n\n// breakpoints\n@mixin media-mobile-S {\n  @media screen and (min-width: 374px) and (max-width: 460px){\n  @content;\n    }\n}\n\n@mixin media-mobile-M {\n  @media screen and (min-width: 461px) and (max-width: 520px){\n  @content;\n    }\n}\n\n@mixin media-mobile-L {\n  @media screen and (min-width: 521px) and (max-width: 600px){\n    @content;\n  }\n}\n\n@mixin media-mobile-XL {\n  @media screen and (min-width: 601px) and (max-width: 767px){\n  @content;\n  }\n}\n\n@mixin media-tablet {\n  @media screen and (min-width: 768px) and (max-width: 991px) {\n  @content;\n  }\n}\n\n@mixin media-desktop {\n  @media screen and (min-width: 992px) and (max-width: 1280px){\n  @content;\n  }\n}\n@mixin media-desktop-1680px {\n  @media screen and (min-width: 1281px) {\n  @content\n  }\n}\n\n@mixin media-desktop-big {\n  @media screen and (min-width: 1681px) {\n  @content\n  }\n}\n\n//landscape\n\n@mixin orientation-landscape {\n  @media screen and (orientation: landscape) and (max-width: 991px){\n    @content\n  }\n}\n@mixin only-mobiles-landscape {\n  @media screen and (orientation: landscape) and (max-width: 767px) {\n    @content\n  }\n}\n@mixin only-tablet-landscape {\n  @media screen and (min-width: 768px) and (max-width: 991px) and (orientation: landscape){\n    @content;\n  }\n}\n//all mobiles\n@mixin all-mobiles {\n  @media screen and (min-width: 374px) and (max-width: 767px) {\n    @content\n  }\n}\n//all big screens\n@mixin all-big-screens {\n  @media screen and (min-width: 992px) {\n    @content\n  }\n}\n\n//iphoneX pixel2xl\n@mixin iPhonexAndPixel2XL{\n  @media screen and (min-width: 800px) and (max-width: 825px),  handheld and (orientation: landscape){\n    @content\n  }\n}\n\n","//colors\n$main-font-color: white;\n$navBar-color: rgba(0,0,0,0.6);\n\n//small devices margin\n\n$margin-mobile: 0 3%;","@import \"../../../../sass/layout/mixins\";\n@import \"../../../../sass/layout/variables\";\n\n$navbarHeight: 11%;\n.navigation {\n  background-color: $navBar-color;\n  height: $navbarHeight;\n  position: fixed;\n  width: 100%;\n  z-index: 2;\n  @include media-tablet {\n    height: 10%;\n  }\n  @include orientation-landscape {\n    height: 20%;\n  }\n\n  @include all-big-screens {\n    height: 120px;\n  }\n  @include all-big-screens {\n    background-color: rgba(0,0,0,0.28);;\n  }\n  div.wrapper {\n    display: flex;\n    flex-direction: column;\n    @include all-big-screens {\n      flex-direction: row;\n     justify-content: space-between;\n      align-items: center;\n    }\n  }\n  .nav-wrapper {\n    display: flex;\n    height: 100%;\n    .logo {\n      img {\n        height: 80%;\n        width: 80%;\n        @include all-big-screens {\n          height: 100%;\n          width: 100%;\n        }\n      }\n    }\n    .hamburger {\n     @include all-big-screens {\n       display: none;\n     }\n      .fas {\n        font-size: 2.5rem;\n        margin: 1rem;\n        cursor: pointer;\n        color: black;\n        @include media-tablet {\n          font-size: 3rem;\n          margin: 1.5rem;\n        }\n      }\n    }\n  }\n  ul.list {\n    position: fixed;\n    width: 100%;\n    height: (100 - $navbarHeight);\n    top:$navbarHeight;\n    background-color: $navBar-color;\n    clip-path: circle(80px at 0 -45%);\n    transition: clip-path 1s ease-out;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-around;\n    @include media-tablet {\n      height: 90%;\n      top: 10%;\n    }\n    @include orientation-landscape {\n      height: 80%;\n      top: 20%;\n      clip-path: circle(80px at 0 -35%);\n    }\n    @include all-big-screens {\n      clip-path: none;\n      background-color: transparent;\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n      position: relative;\n      height: 100%;\n      top:0;\n    }\n    li {\n      text-align: center;\n      font-size: 1.5rem;\n      @include media-mobile-S {\n        font-size: 2rem;\n      }\n      @include media-tablet {\n        font-size: 1.5rem;\n      }\n      @include media-desktop {\n        font-size: 1.2rem;\n      }\n      @include media-desktop-1680px {\n        font-size: 1.4rem;\n      }\n      @include media-desktop-big {\n        font-size: 1.4em;\n      }\n      a {\n        text-decoration: none;\n        color: $main-font-color;\n        &:hover {\n          color: black;\n          text-decoration: underline;\n        }\n      }\n    }\n  }\n}\nul.list.open {\n  clip-path: circle(1000px at 0% -15%);\n  @include media-tablet {\n    clip-path: circle(1300px at 0% -15%);\n  }\n  @include all-big-screens {\n    clip-path: circle(5000px at 0% -15%);\n  }\n}\n","footer {\n  height: 10vh;\n  width: 100%;\n  background-color: black;\n}\nfooter div {\n  color: white;\n}","@import \"../../../../sass/layout/mixins\";\n@import \"../../../../sass/layout/variables\";\n\n.welcome {\n  font-size: 2rem;\n  text-align: center;\n  margin: 2% auto;\n  font-family: 'Pacifico', cursive;\n  @include media-tablet {\n    font-size: 3rem;\n  }\n  @include all-big-screens {\n    font-size: 3rem;\n  }\n  @include orientation-landscape {\n    font-size: 2rem;\n    margin-top: 11%;\n  }\n}\np.welcome-content {\n  font-size: 1.15rem;\n  font-weight: 600;\n  @include media-mobile-S {\n  font-size: 1.45rem;\n  }\n  @include media-mobile-L {\n    font-size: 1.65rem;\n  }\n  @include media-tablet {\n    font-size: 2rem;\n  }\n  @include all-big-screens {\n    font-size: 1.5rem;\n  }\n  @include orientation-landscape {\n    font-size: 1rem;\n  }\n}\n\n.imageWrapper {\n  display: flex;\n  width: 100%;\n  height: 140px;\n  flex-flow: column wrap;\n  overflow-x: scroll;\n  @include orientation-landscape {\n    flex-flow: row;\n    justify-conten